.vp-article-wrapper {
  position: relative;
  box-sizing: border-box;
  width: 100%;
  margin: 0 auto 1.25rem;
  text-align: start;
  overflow-wrap: break-word;
}
@media (max-width: 959px) {
  .vp-article-wrapper {
    margin: 0 auto 1rem;
  }
}
.vp-article-wrapper:last-child {
  margin-bottom: 0;
}
.vp-article-wrapper:hover .vp-article-title::after {
  visibility: visible;
  transform: scaleX(1);
}

.vp-article-item {
  display: block;
  padding: 0.75rem 1.25rem;
  border-radius: 0.4rem;
  background: var(--vp-c-bg-elv);
  color: inherit;
  box-shadow: 0 1px 3px 1px var(--vp-c-shadow);
  transition: background var(--vp-t-color), box-shadow var(--vp-t-transform);
}
@media (max-width: 959px) {
  .vp-article-item {
    padding: 0.75rem 1rem;
  }
}
@media (max-width: 419px) {
  .vp-article-item {
    border-radius: 0;
  }
}
.vp-article-item:hover {
  box-shadow: 0 2px 6px 2px var(--vp-c-shadow);
}
.vp-article-item .sticky-icon {
  position: absolute;
  inset-inline-end: 0;
  top: 0;
  width: 1.5rem;
  height: 1.5rem;
  color: var(--vp-c-accent);
}
.vp-article-item .page-info > span {
  display: flex;
  flex-shrink: 0;
  align-items: center;
  margin-inline-end: 0.5em;
  line-height: 1.8;
}
.vp-article-item .page-info > span::after {
  --balloon-font-size: 12px;
  padding: 0.3em 0.6em !important;
}

.vp-article-hr {
  margin-block: 0.375em;
}

.vp-article-title {
  position: relative;
  display: inline-block;
  color: var(--vp-c-text);
  font-size: 1.25rem;
  font-family: var(--vp-font-heading);
  line-height: 1.6;
  cursor: pointer;
}
.vp-article-title::after {
  content: "";
  position: absolute;
  inset: auto 0 0;
  height: 2px;
  background: var(--vp-c-accent);
  visibility: hidden;
  transition: transform var(--vp-t-transform);
  transform: scaleX(0);
}
.vp-article-title a {
  color: inherit;
  font-weight: 600;
}
.vp-article-title .lock-icon,
.vp-article-title .slides-icon {
  position: relative;
  bottom: -0.125em;
  display: inline-block;
  vertical-align: baseline;
  width: 1em;
  height: 1em;
  margin-inline-end: 0.25em;
  color: var(--vp-c-accent);
}
.vp-article-title > span {
  word-break: break-word;
}

.vp-article-cover {
  width: calc(100% + 2.5rem);
  margin: -0.75rem -1.25rem 0.75rem;
  border-top-left-radius: 0.4rem;
  border-top-right-radius: 0.4rem;
}
@media (max-width: 959px) {
  .vp-article-cover {
    width: calc(100% + 2rem);
    margin: -0.75rem -1rem 0.75rem;
  }
}
@media (max-width: 419px) {
  .vp-article-cover {
    border-radius: 0;
  }
}

.vp-article-excerpt {
  --code-padding-y: 0.75rem;
  --code-padding-x: 0.75rem;
  --code-line-number-width: 2em;
  overflow: hidden;
  line-height: 1.6;
  cursor: default;
}
@media (max-width: 959px) {
  .vp-article-excerpt {
    font-size: 15px;
  }
}
@media (max-width: 419px) {
  .vp-article-excerpt {
    font-size: 14px;
  }
}
.vp-article-excerpt h1:first-child {
  display: none;
}
.vp-article-excerpt h2 {
  font-size: 1.2em;
}
.vp-article-excerpt h3 {
  font-size: 1.15em;
}
.vp-article-excerpt h1,
.vp-article-excerpt h2,
.vp-article-excerpt h3,
.vp-article-excerpt h4,
.vp-article-excerpt h5,
.vp-article-excerpt h6 {
  margin-top: 0.5em;
  margin-bottom: 0.5em;
}
.vp-article-excerpt p,
.vp-article-excerpt ul,
.vp-article-excerpt ol {
  line-height: 1.5;
}
.vp-article-excerpt p {
  margin-block: 0.5em;
}
.vp-article-excerpt div[class*=language-]::before {
  top: 0;
}
.vp-article-excerpt div[class*=language-] pre, .vp-article-excerpt div[class*=language-].line-numbers-mode .line-numbers {
  line-height: 1.5;
}
.vp-article-excerpt .code-demo-wrapper {
  display: none;
}
.vp-article-excerpt .footnote-anchor {
  display: none;
}
.vp-article-excerpt section.footnotes {
  display: none;
}
.vp-article-excerpt img {
  max-width: 100%;
}
.vp-article-excerpt figure {
  display: flex;
  flex-flow: column;
  width: auto;
  margin: 1rem auto;
  text-align: center;
  transition: transform var(--vp-t-transform);
}
.vp-article-excerpt figure img {
  overflow: hidden;
  margin: 0 auto;
  border-radius: 8px;
}
.vp-article-excerpt figure figcaption {
  display: inline-block;
  margin: 6px auto;
  font-size: 0.8rem;
}
.vp-article-excerpt figure figcaption:only-child {
  display: none;
}

.vp-article-list {
  margin-top: calc(-0.5rem - var(--navbar-height));
  padding-top: calc(var(--navbar-height) + 0.5rem);
  text-align: center;
}
.vp-article-list:first-child {
  margin-top: calc(0rem - var(--navbar-height));
}

.vp-empty-hint {
  text-align: center;
}

.vp-article-type-wrapper {
  position: relative;
  z-index: 2;
  display: flex;
  align-items: center;
  justify-content: center;
  padding-inline-start: 0;
  list-style: none;
  font-weight: 600;
  font-size: 18px;
}
@media (max-width: 419px) {
  .vp-article-type-wrapper {
    font-size: 16px;
  }
}

.vp-article-type {
  position: relative;
  vertical-align: middle;
  margin: 0.3em 0.8em;
  line-height: 1.2;
  cursor: pointer;
}
.vp-article-type::after {
  content: " ";
  position: absolute;
  inset: auto 50% -6px;
  height: 2px;
  border-radius: 1px;
  background: var(--vp-c-accent-bg);
  visibility: hidden;
  transition: inset 0.2s ease-in-out;
}
.vp-article-type a {
  display: inline-block;
  color: inherit;
  transition: all 0.3s ease-in-out;
}
.vp-article-type.active {
  position: relative;
}
.vp-article-type.active a {
  color: var(--vp-c-accent);
  transform: scale(1.1, 1.1);
}
.vp-article-type:hover::after, .vp-article-type.active::after {
  inset: auto calc(50% - 8px) -6px;
  visibility: visible;
}

.vp-blog-hero {
  position: relative;
  display: flex;
  flex-flow: column;
  justify-content: center;
  height: 450px;
  margin-bottom: 1rem;
  color: #eee;
  font-family: var(--vp-font-heading);
}
@media (max-width: 719px) {
  .vp-blog-hero {
    height: 350px;
  }
}
@media (max-width: 419px) {
  .vp-blog-hero {
    margin: 0 0 1rem;
  }
}
.vp-blog-hero.no-bg {
  color: var(--vp-c-text);
}
.vp-blog-hero > :not(.vp-blog-mask) {
  position: relative;
  z-index: 2;
}
.vp-blog-hero .slide-down-button {
  border-width: 0;
  background: transparent;
  cursor: pointer;
  position: absolute;
  bottom: 0;
  left: calc(50vw - 30px);
  display: none;
  width: 60px;
  height: 60px;
  padding: 10px;
}
.vp-blog-hero .slide-down-button .icon {
  width: 30px;
  margin: -15px 0;
  animation-name: bounce-down;
  animation-duration: 1.5s;
  animation-timing-function: linear;
  animation-direction: alternate;
  animation-iteration-count: infinite;
}
.vp-blog-hero .slide-down-button .icon:first-child {
  color: rgba(255, 255, 255, 0.15);
}
.vp-blog-hero .slide-down-button .icon:last-child {
  color: rgba(255, 255, 255, 0.5);
}
.vp-blog-hero.fullscreen {
  height: calc(100vh - var(--navbar-height));
}
.vp-blog-hero.fullscreen .vp-blog-mask {
  background-position-y: top;
}
.vp-blog-hero.fullscreen .slide-down-button {
  display: block;
}

.vp-blog-mask {
  position: absolute;
  inset: 0;
}
.vp-blog-mask::after {
  content: " ";
  position: absolute;
  inset: 0;
  z-index: 1;
  display: block;
  background: var(--vp-c-grey-soft);
}
.vp-blog-mask.light {
  display: block;
}
[data-theme=dark] .vp-blog-mask.light {
  display: none;
}
.vp-blog-mask.dark {
  display: none;
}
[data-theme=dark] .vp-blog-mask.dark {
  display: block;
}

.vp-blog-hero-title {
  margin: 0.5rem auto;
  font-weight: bold;
  font-size: 2rem;
}
@media (min-width: 1440px) {
  .vp-blog-hero-title {
    font-size: 2.25rem;
  }
}
@media (max-width: 719px) {
  .vp-blog-hero-title {
    font-size: 1.75rem;
  }
}

.vp-blog-hero-image {
  display: block;
  max-width: 100%;
  max-height: 15rem;
  margin: 1.5rem auto;
}
@media (max-width: 719px) {
  .vp-blog-hero-image {
    max-height: 12rem;
  }
}
.vp-blog-hero-image.light {
  display: block;
}
[data-theme=dark] .vp-blog-hero-image.light {
  display: none;
}
.vp-blog-hero-image.dark {
  display: none;
}
[data-theme=dark] .vp-blog-hero-image.dark {
  display: block;
}
.vp-blog-hero-image + .vp-blog-hero-title {
  margin: 0 auto;
}

.vp-blog-hero-description {
  margin: 1.2rem auto 0;
  font-size: 1.5rem;
}
@media (max-width: 719px) {
  .vp-blog-hero-description {
    font-size: 1.25rem;
  }
}

@keyframes bounce-down {
  from {
    transform: translateY(-5px);
  }
  to {
    transform: translateY(5px);
  }
}
.vp-blogger-info {
  padding: 0.5rem;
  font-family: var(--vp-font-heading);
  overflow-wrap: break-word;
}
.vp-page .vp-blogger-info {
  background: var(--vp-c-bg-elv);
}
.vp-sidebar .vp-blogger-info.mobile {
  display: none;
}
@media (max-width: 719px) {
  .vp-sidebar .vp-blogger-info.mobile {
    display: block;
  }
}
.vp-sidebar .vp-blogger-info.mobile + hr {
  display: none;
}
@media (max-width: 719px) {
  .vp-sidebar .vp-blogger-info.mobile + hr {
    display: block;
    margin-top: 1rem;
  }
}

.vp-blogger {
  padding: 0.5rem;
  text-align: center;
}
.vp-blogger[role=link] {
  cursor: pointer;
}

.vp-blogger-avatar {
  width: 8rem;
  height: 8rem;
  margin: 0 auto;
}

.vp-blogger-name {
  margin: 1rem auto;
  font-size: 22px;
}

.vp-blogger-description {
  margin: 1rem auto;
  font-size: 14px;
}

.vp-blog-counts {
  display: flex;
  justify-content: space-evenly;
  width: 80%;
  margin: 0 auto 1rem;
}

.vp-blog-count {
  display: block;
  color: inherit;
  font-size: 13px;
  text-align: center;
  cursor: pointer;
  transition: color var(--vp-t-color);
}
.vp-blog-count:hover {
  color: var(--vp-c-accent);
}
.vp-blog-count .count {
  position: relative;
  margin-bottom: 0.5rem;
  font-weight: 600;
  font-size: 20px;
}

.vp-category-list {
  position: relative;
  z-index: 2;
  padding-inline-start: 0;
  list-style: none;
  font-size: 14px;
}

.vp-category-item {
  display: inline-block;
  margin: 0.25rem 0.5rem 0.5rem;
}

.vp-category {
  display: inline-block;
  vertical-align: middle;
  overflow: hidden;
  padding: 0.25rem 0.75rem;
  border-radius: 0.25rem;
  background: var(--vp-c-grey-soft);
