[data-theme=dark] .timeline-list-wrapper {
  --dot-color: #444;
  --dot-bar-color: #333;
  --dot-border-color: #555;
}
.timeline-list-wrapper .timeline-list-title {
  cursor: pointer;
}
.timeline-list-wrapper .timeline-list-title .icon {
  position: relative;
  bottom: -0.125rem;
  width: 16px;
  height: 16px;
  margin: 0 6px;
}
.timeline-list-wrapper .timeline-list-title .num {
  position: relative;
  margin: 0 2px;
  font-size: 22px;
}
.timeline-list-wrapper .timeline-content {
  overflow-y: auto;
  max-height: 80vh;
}
.timeline-list-wrapper .timeline-content::-webkit-scrollbar-track-piece {
  background: transparent;
}
.timeline-list-wrapper .timeline-list {
  position: relative;
  box-sizing: border-box;
  margin: 0 8px;
  list-style: none;
}
.timeline-list-wrapper .timeline-list::after {
  content: " ";
  position: absolute;
  inset-inline-start: 0;
  top: 14px;
  z-index: -1;
  width: 4px;
  height: calc(100% - 14px);
  margin-inline-start: -2px;
  background: var(--dot-bar-color);
  transition: background var(--vp-t-color);
}
.timeline-list-wrapper .timeline-year {
  position: relative;
  margin: 20px 0 0;
  color: var(--vp-c-text);
  font-weight: 700;
  font-size: 20px;
}
.timeline-list-wrapper .timeline-year::before {
  content: " ";
  position: absolute;
  z-index: 2;
  border: 1px solid var(--dot-border-color);
  border-radius: 50%;
  background: var(--dot-color);
  transition: background var(--vp-t-color), border-color var(--vp-t-color);
  inset-inline-start: -20px;
  top: 50%;
  width: 8px;
  height: 8px;
  margin-top: -4px;
  margin-inline-start: -4px;
}
.timeline-list-wrapper .timeline-year-wrapper {
  padding-inline-start: 0 !important;
}
.timeline-list-wrapper .timeline-date {
  display: inline-block;
  vertical-align: bottom;
  width: 36px;
  font-size: 12px;
  line-height: 32px;
  transition: color var(--vp-t-color);
}
.timeline-list-wrapper .timeline-date::before {
  content: " ";
  position: absolute;
  z-index: 2;
  border: 1px solid var(--dot-border-color);
  border-radius: 50%;
  background: var(--dot-color);
  transition: background var(--vp-t-color), border-color var(--vp-t-color);
  inset-inline-start: -19px;
  top: 24px;
  width: 6px;
  height: 6px;
  margin-inline-start: -4px;
}
.timeline-list-wrapper .timeline-title {
  color: inherit;
  font-size: 14px;
  line-height: 32px;
  cursor: pointer;
  transition: color var(--vp-t-color);
}
.timeline-list-wrapper .timeline-item {
  position: relative;
  display: flex;
  padding: 12px 0 4px;
  border-bottom: 1px dashed var(--vp-c-border);
  list-style: none;
  transition: border-color var(--vp-t-color);
}
.timeline-list-wrapper .timeline-item:hover .timeline-date {
  color: var(--vp-c-accent);
}
.timeline-list-wrapper .timeline-item:hover .timeline-date::before {
  border-color: var(--dot-color);
  background: var(--vp-c-accent);
}
.timeline-list-wrapper .timeline-item:hover .timeline-title {
  color: var(--vp-c-accent);
}