.page-original-info {
  position: relative;
  display: inline-block;
  vertical-align: middle;
  overflow: hidden;
  padding: 0 0.5em;
  border: 0.5px solid var(--vp-c-border-hard);
  border-radius: 0.75em;
  background: var(--vp-c-bg);
  font-size: 0.75em;
  line-height: 1.5 !important;
}

.page-info {
  display: flex;
  flex-wrap: wrap;
  place-content: stretch flex-start;
  align-items: center;
  color: var(--vp-c-text-mute);
  font-size: 14px;
}
@media print {
  .page-info {
    display: flex !important;
  }
}
.page-info > span {
  display: flex;
  align-items: center;
  max-width: 100%;
  margin-inline-end: 0.5em;
  line-height: 2;
}
@media (min-width: 1440px) {
  .page-info > span {
    font-size: 1.1em;
  }
}
@media (max-width: 419px) {
  .page-info > span {
    margin-inline-end: 0.3em;
    font-size: 0.875em;
  }
}
@media print {
  .page-info > span {
    display: flex !important;
  }
}
.page-info .icon {
  position: relative;
  display: inline-block;
  vertical-align: middle;
  width: 1em;
  height: 1em;
  margin-inline-end: 0.25em;
}
.page-info a {
  color: inherit;
}
.page-info a:hover, .page-info a:active {
  color: var(--vp-c-accent);
}

.vp-page-meta {
  max-width: var(--content-width, 740px);
  margin-inline: auto;
  padding-inline: 2.5rem;
}
@media (max-width: 959px) {
  .vp-page-meta {
    padding-inline: 1.5rem;
  }
}
@media print {
  .vp-page-meta {
    max-width: unset;
  }
}
.vp-page-meta {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  overflow: auto;
  padding-top: 0.75rem;
  padding-bottom: 0.75rem;
}
@media print {
  .vp-page-meta {
    margin: 0 !important;
    padding-inline: 0 !important;
  }
}
@media (max-width: 719px) {
  .vp-page-meta {
    display: block;
  }
}
.vp-page-meta .vp-meta-item {
  flex-grow: 1;
}
.vp-page-meta .vp-meta-item .vp-meta-label {
  font-weight: 500;
}
.vp-page-meta .vp-meta-item .vp-meta-label:not(a) {
  color: var(--vp-c-text);
}
.vp-page-meta .vp-meta-item .vp-meta-info {
  color: var(--vp-c-text-mute);
  font-weight: 400;
}
.vp-page-meta .git-info {
  text-align: end;
}
.vp-page-meta .edit-link {
  margin-top: 0.25rem;
  margin-bottom: 0.25rem;
  margin-inline-end: 0.5rem;
  font-size: 14px;
}
@media print {
  .vp-page-meta .edit-link {
    display: none;
  }
}
.vp-page-meta .edit-link .icon {
  position: relative;
  bottom: -0.125em;
  width: 1em;
  height: 1em;
  margin-inline-end: 0.25em;
}
.vp-page-meta .update-time,
.vp-page-meta .contributors {
  margin-top: 0.25rem;
  margin-bottom: 0.25rem;
  font-size: 14px;
}
@media (max-width: 719px) {
  .vp-page-meta .update-time,
  .vp-page-meta .contributors {
    font-size: 13px;
    text-align: start;
  }
}

.print-button {
  border-width: 0;
  background: transparent;
  cursor: pointer;
  box-sizing: content-box;
  width: 1rem;
